<template>
  <div>
    <v-sheet color="rgba(0,0,0,0.6)" min-height="600">
      <v-container>
        <v-row>
          <v-col cols="12" class="text-center white--text">
            <h1>ADMINISTRAR DOCENTES</h1>
          </v-col>
        </v-row>
        <v-row>
          <v-col
        class="d-flex"
        cols="12"
        sm="6"
      >
        <v-select
          :items="Nombres_Docentes"
          label="Docentes"
          v-model="Docente_Seleccionado"
          dense
          solo
          clearable
          @change="Cargando_Datos()"
        ></v-select>
      </v-col>
        </v-row>
        <v-row>
          <Datos_Docente :editar_empleado="true" :no_trabajador="id" />
        </v-row>
      </v-container>
    </v-sheet>
  </div>
</template>
<script>
import axios from "axios";
import { mapMutations, mapState } from "vuex";
import Datos_Docente from '../components/Datos_Docente.vue';

export default {
  components: { Datos_Docente },
  mounted() {
    //this.Falta_Justificadas()
    //this.Traer_Docentes();
    this.Traer_Docentes_FALTAS()
  },
  data() {
    return {
      Docentes:[],
      Nombres_Docentes:[],
      Docente_Seleccionado:"",
      id:""
    };
  },
  computed: {
    ...mapState(["ip"]),
  },
  watch: {},
  methods: {
    ...mapMutations([
      "Modificar_cargar",
      "Checar_Token",
      "Salir",
      "Modificar_Informacion",
      "Modificar_Espera",
      "Modificar_Correcto",
      "Modificar_Datos",
      "Modificar_Boton",
      "Modificar_ruta",
    ]),

    async Traer_Docentes() {
      this.Docentes=[]
      this.id_Docentes=[]
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Cargando Faltas");
      this.Modificar_Espera(true);

      try {
        const json = JSON.stringify({
          token: localStorage.getItem("Token"),
        });
        //      console.log(json);
        // let res = await axios.post(
        //   "http://" + this.ip + "/api/jefe_departamento/Falta_Justificada",
        //   json,
        //   {
        //     headers: {
        // Overwrite Axios's automatically set Content-Type
        //       "Content-Type": "application/json",
        //     },
        //   }
        // );
        let res = await axios.post(
          "http://" + this.ip + "/api/docente/all",
          json,
          {
            headers: {
              // Overwrite Axios's automatically set Content-Type
              "Content-Type": "application/json",
            },
          }
        );
        
        if (!this.error && !res.data.Error) {
          console.log(res.data);
          const Docentes = res.data.Datos;
          const Nombre_Docentes= []
          res.data.Datos.forEach(element => {
            Nombre_Docentes.push(element.Nombre)
          });
          this.Docentes=Docentes;          
          this.Nombres_Docentes = Nombre_Docentes;
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);
         this.Modificar_cargar(false); 
          
        } else if (res.data.Error) {
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);
          this.Modificar_Datos(res.data.status);
          this.Modificar_Boton(true);
        }
      } catch (error) {
        this.Modificar_Espera(false);
        this.Modificar_Correcto(false);
        this.Modificar_Boton(true);

        console.log(error);
      }

    },
    Cargando_Datos(){
      if(this.Docente_Seleccionado){
        console.log("Mira");
        const dato = this.Docentes.find(element => element.Nombre == this.Docente_Seleccionado)
        console.log(dato.No_Empleado);
        this.id = dato.No_Empleado;
      }
      
    },
    

  },
};
</script>