<template>
  <v-container>
    <v-sheet color="rgba(0,0,0,0.7)" rounded>
      <v-row class="white--text">
        <v-col cols="12" class="text-center">
          <span class="white--text text-md-h3 text-h4 text-uppercase"
            >Información</span
          >
        </v-col>
      </v-row>
      <v-row class="white--text">
        <v-col cols="12" class="text-center white--text">
          <v-sheet color="rgba(21,21,24,0.6)" rounded>
            <v-container class="">
              <v-row>
                <v-col>
                  <span class="white--text">Datos Personales</span>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" md="4">                  
                  <v-text-field
                    plain
                    v-model="N_Datos.num_trabajador"                    
                    label="Numero De Trabajador"
                    dark   
                    :disabled="!editar_no_empleado"
                  ></v-text-field>                  
                </v-col>
                <v-col cols="12" md="2">                  
                  <v-text-field
                    v-model="N_Datos.Nombre"                  
                    dark
                    label="Nombre"
                    maxlength="15"
                    required
                    :disabled="(no_trabajador ? !editar_empleado:false)"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="2">                  
                  <v-text-field
                    v-model="N_Datos.ApellidoP"                  
                    dark
                    label="Apellido Paterno"
                    maxlength="15"
                    required
                    :disabled="(no_trabajador ? !editar_empleado:false)"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="2">                  
                  <v-text-field
                    v-model="N_Datos.ApellidoM"                  
                    dark
                    maxlength="15"
                    label="Apellido Materno"
                    :disabled="(no_trabajador ? !editar_empleado:false)"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="2">                  
                  <v-text-field
                    v-model="N_Datos.correo"                  
                    dark
                    maxlength="50"
                    label="Correo Electronico"
                    :rules="[rules.email]"
                    required
                    :disabled="(no_trabajador ? !editar_empleado:false)"
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" md="5">
                  <v-text-field
                    v-model="N_Datos.CURP"                  
                    dark
                    label="CURP"                
                    maxlength="25"    
                    :disabled="(no_trabajador ? !editar_empleado:false)"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="5">
                  <v-text-field
                    v-model="N_Datos.Telefono"                  
                    dark
                    label="Telefono"
                    maxlength="12"
                    :rules="[rules.telefono]"
                    :disabled="(no_trabajador ? !editar_empleado:false)"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="2" class="pa-1" v-if="Se_Termino">
                      <v-btn  color="green"  outlined @click="Actualizar_Datos()"> Guardar Cambios</v-btn>                  
                </v-col>
                
              </v-row>
            </v-container>
          </v-sheet>
        </v-col>
      </v-row>
    </v-sheet>
  </v-container>
</template>
<script>
import { mapState , mapMutations} from 'vuex';
import axios from "axios";
export default {
  watch: {
    no_trabajador: function () {
      this.Cargar_Datos()
    },
    },
    props:{
        no_trabajador:{
            default:null
        },
        editar_no_empleado:{
            default:false
        },
        editar_empleado:{
            default: false
        }
    },
  
  mounted() {
    this.Cargar_Datos()
  },
  computed: {
    ...mapState(['ip']),
    Se_Termino:function(){
      const pattern = /^[0-9]*$/      
      const pattern2 = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      
      if(pattern.test(this.N_Datos.Telefono) && pattern2.test(this.N_Datos.correo)){
        
        if(this.N_Datos.Nombre.length > 0 && this.N_Datos.ApellidoP.length > 0 && this.N_Datos.ApellidoM.length >= 0 && this.N_Datos.Telefono.length >= 9 && this.N_Datos.CURP.length > 1){
          
          return this.N_Datos.Nombre != this.Persona.Nombre || this.N_Datos.ApellidoP != this.Persona.ApellidoP || this.N_Datos.ApellidoM != this.Persona.ApellidoM || this.N_Datos.Telefono != this.Persona.Telefono || this.Persona.correo != this.N_Datos.correo || this.Persona.CURP != this.N_Datos.CURP
        } 
        else{
          return false
        }      
      }else{
        
        return false
      }

    }
  },
  data() {
    return {      
      Persona:{
        Nombre:"",
        ApellidoP:"",
        ApellidoM:"",
        correo:"",
        CURP:"",
        Telefono:"",
        num_trabajador: ""},
        
      N_Datos:{
        Nombre:"",
        ApellidoP:"",
        ApellidoM:"",
        correo:"",
        CURP:"",
        Telefono:"",
        num_trabajador:""
      },
      rules: {
          
          telefono: value => {
            const pattern = /^[0-9]*$/
            if(value)
            return pattern.test(value) || 'Caracteres No Validos'
            else
            return false
          },
          email: value => {
            const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            return pattern.test(value) || 'e-mail Invalido'
          }
       },
    }
  },
  methods: {    
    ...mapMutations(["Modificar_cargar"
    , "Modificar_login",
    "Actualizar_Nombre"
    ,"Actualizar_No_Trabajador"
    ,"Actualizar_Tipo_User",
    "Actualizar_No_JD",
    "Actualizar_Token"
    ,"Checar_Token",
    "Salir",
    "Modificar_Informacion",
    "Modificar_Espera",
    "Modificar_Correcto",
    "Modificar_Datos",
    "Modificar_Boton",
    "Modificar_ruta"
    ]),
    async Cargar_Datos() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Comprobando Usuario Y Contraseña")
      this.Modificar_Espera(true);      
      const json = JSON.stringify({
        token : localStorage.getItem("Token"),
        id: this.no_trabajador,
      });
      
      try {
        let res = await axios.post("http://"+this.ip+"/api/docente/datos", json, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "application/json",
          },
        })        
        if(!this.error && !res.data.Error){          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);          
          this.Modificar_Informacion("Correcta");            
          
           this.Persona={
        Nombre:res.data.Datos.Nombre,
        ApellidoP:res.data.Datos.ApellidoP,
        ApellidoM:res.data.Datos.ApellidoM,
        correo:res.data.Datos.Correo,
        CURP:res.data.Datos.CURP,
        Telefono:res.data.Datos.Telefono,
        num_trabajador: res.data.Datos.No_Empleado}
        this.N_Datos={
        Nombre:res.data.Datos.Nombre,
        ApellidoP:res.data.Datos.ApellidoP,
        ApellidoM:res.data.Datos.ApellidoM,
        correo:res.data.Datos.Correo,
        CURP:res.data.Datos.CURP,
        Telefono:res.data.Datos.Telefono,
        num_trabajador: res.data.Datos.No_Empleado}
        
          // this.Actualizar_No_Trabajador(res.data.Datos.No_Empleado+"");
          // this.Actualizar_Nombre(res.data.Datos.Nombre)     
          // this.Actualizar_Tipo_User(res.data.Datos.User_Type)
          // this.Actualizar_No_JD(res.data.Datos.No_JD)                    
          this.Modificar_Boton(true)
        }
        else if(res.data.Error){
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);  
          this.error=true;
          this.Modificar_Datos(res.data.status);
          this.Salir();
          this.Modificar_Boton(true)
        }  
      } catch (error) {
        this.Modificar_Espera(false);
          this.Modificar_Correcto(false);  
          this.Modificar_Boton(true)
          console.log(error);
          this.Salir();
      }          
    },
    async Actualizar_Datos() {
      this.Modificar_cargar(true);
      this.Modificar_Informacion("Mandando Actualizaciones")
      this.Modificar_Espera(true);      
      const json = JSON.stringify({
        token : localStorage.getItem("Token"),
        id: this.no_trabajador,
        Nombre: this.N_Datos.Nombre,
        ApellidoP: this.N_Datos.ApellidoP,
        ApellidoM: this.N_Datos.ApellidoM,
        CURP: this.N_Datos.CURP,
        Correo: this.N_Datos.correo,
        Telefono: parseInt(this.N_Datos.Telefono,10),
      });
      
      try {
        let res = await axios.put("http://"+this.ip+"/api/docente/datos", json, {
          headers: {
            // Overwrite Axios's automatically set Content-Type
            "Content-Type": "application/json",
          },
        })        
        if(!this.error && !res.data.Error){          
          this.Modificar_Espera(false);
          this.Modificar_Correcto(true);          
          this.Modificar_Informacion("Correcta La Carga");            
          
           
        
          // this.Actualizar_No_Trabajador(res.data.Datos.No_Empleado+"");
          // this.Actualizar_Nombre(res.data.Datos.Nombre)     
          // this.Actualizar_Tipo_User(res.data.Datos.User_Type)
          // this.Actualizar_No_JD(res.data.Datos.No_JD)                    
          this.Modificar_Boton(false)
          this.Cargar_Datos()
        }
        else if(res.data.Error){
          this.Modificar_Espera(false);
          this.Modificar_Correcto(false);  
          this.error=true;
          this.Modificar_Datos(res.data.status);
          this.Salir();
          this.Modificar_Boton(true)
        }  
      } catch (error) {
        this.Modificar_Espera(false);
          this.Modificar_Correcto(false);  
          this.Modificar_Boton(true)
          console.log(error);
          this.Salir();
      }          
    }
  },
};
</script>